import { urlObjectKeys } from "next/dist/next-server/lib/utils";

class Index extends React.Component {
    render() {
        return (
            <div class="hero-image" style={heroImage}>
                <img src="https://blog.tmcnet.com/blog/rich-tehrani/wp-content/uploads/2019/12/Technology-AdobeStock_294390642-scaled.jpg"></img>
            </div>
        )
    }
}
export default Index
const heroImage = {
    position: "absolute",
    Width: "20%",
    Height: "100vw",
    top: "0px",
    bottom: "0px",
    left: "0px",
    right: "0px"
}
const heroText = {
    textAlign: "center",
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    color: "white"
}